.dpd-parcelshop-map-widget {
	display: none;
	position: fixed;
	z-index: 999;
	width: 100%;
	height: 100%;
	background: rgb(0 0 0 / 50%);
	top: 0;
	left: 0;
	z-index: 100000;

	// Force stronger selector to override external styles
	&.active {
		display: block;
	}

	& #{&} {
		&__container {
			-ms-flex-item-align: center;
			-ms-grid-row-align: center;
			align-self: center;
			position: relative;
			height: 100%;
			width: 100%;
			padding: 15px;
		}

		&__embed {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
			-ms-flex-direction: column;
			flex-direction: column;
			position: relative;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			-ms-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			width: 100%;
			height: 100%;
			overflow: hidden;
			border-radius: 0px;
			background: #ffffff;
			padding: 15px;
		}
	}
}
